<template>
    <div class="cart">
        <mooc-mall-header></mooc-mall-header>
        <mooc-mall-bread>
            CART
        </mooc-mall-bread>
        <div class="my-cart-main">
            <div class="container">
                <div class="cart-top-text">
                    <h1>MY CART</h1>
                </div>
                <div class="cart-item-wrapper">
                    <ul>
                        <li>ITEMS</li>
                        <li>PRICE</li>
                        <li>QUANTITY</li>
                        <li>SUBTOTAL</li>
                        <li>EDIT</li>
                    </ul>
                </div>
                <div class="cart-item-list-wrapper">
                    <div class="cart-item-list">
                        <ul>
                            <li class="cart-item-li-product" v-for="(item, index) in cartList">
                                <div class="cart-tab-desc">
                                    <span class="cart-tab-desc-check">
                                        <div class="cart-item-icon" @click="editCart('checked',item)"
                                             :class="{'activeChecked': item.checked=='1'}">
                                            <svg class="icon" v-if="item.checked=='1'"
                                                 :class="{'activeCheckedIcon':item.checked=='1'}"
                                                 aria-hidden="true">
                                              <use xlink:href="#iconzhengque"></use>
                                            </svg>
                                        </div>
                                    </span>
                                    <span class="cart-tab-desc-pic"><img :key="item.productImage"
                                                                         v-lazy="'productsImg/'+item.productImage"></span>
                                    <span class="cart-tab-desc-title">{{item.productName}}</span>
                                </div>
                                <div class="cart-tab-price">{{item.salePrice | currency('¥')}}</div>
                                <div class="cart-tab-quantity">
                                    <el-input-number @change="editCart($event, item)" size="small"
                                                     v-model="item.productNum" :min="1"></el-input-number>
                                </div>
                                <div class="cart-tab-subtotal">{{item.salePrice * item.productNum | currency('¥')}}
                                </div>
                                <div class="cart-tab-edit" @click="delCartItemConfrim(item._id)">
                                    <svg class="icon icon-delete">
                                        aria-hidden="true">
                                        <use xlink:href="#icondelete"></use>
                                    </svg>
                                </div>
                            </li>
                            <li v-if="cartList.length==0" class="noProduct">
                                <Icon color="#EE7A23" size="85" type="ios-sad-outline"/>
                                <div class="no-product-span">还没有商品呢, 去添加一些吧!</div>
                                <div class="go-goods-list-btn-wrapper">
                                    <span @click="$router.replace('/')" class="go-goods-list-btn">goodslist</span>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="cart-item-list-wrapper-response">
                    <div class="cart-item-list">
                        <ul>
                            <li class="cart-item-li-product" v-for="(item, index) in cartList">
                                <div class="product-top">
                                    <div class="product-top-left">
                                        <span class="left-checked-icon">
                                            <div class="cart-item-icon" @click="editCart('checked',item)"
                                                 :class="{'activeChecked': item.checked=='1'}">
                                                <svg class="icon" v-if="item.checked=='1'"
                                                     :class="{'activeCheckedIcon':item.checked=='1'}"
                                                     aria-hidden="true">
                                                    <use xlink:href="#iconzhengque"></use>
                                                </svg>
                                            </div>
                                        </span>
                                        <span class="product-img">
                                            <img :key="item.productImage"
                                                 v-lazy="'productsImg/'+item.productImage">
                                        </span>
                                        <span class="product-name">{{item.productName}}</span>
                                    </div>
                                    <div class="product-right-del-btn-wrapper">
                                        <div class="cart-tab-edit" @click="delCartItemConfrim(item._id)">
                                            <svg class="icon icon-delete">
                                                aria-hidden="true">
                                                <use xlink:href="#icondelete"></use>
                                            </svg>
                                        </div>
                                    </div>
                                </div>
                                <div class="product-bottom">
                                    <div class="product-bottom-left-inputnumber">
                                        <el-input-number @change="editCart($event, item)" size="small"
                                                         v-model="item.productNum" :min="1"></el-input-number>
                                    </div>
                                    <div class="product-right-totalprice">{{item.salePrice * item.productNum |
                                        currency('¥')}}
                                    </div>
                                </div>
                            </li>
                            <li v-if="cartList.length==0" class="noProduct">
                                <Icon color="#EE7A23" size="85" type="ios-sad-outline"/>
                                <div class="no-product-span">还没有商品呢, 去添加一些吧!</div>
                                <div class="go-goods-list-btn-wrapper">
                                    <span @click="$router.replace('/')" class="go-goods-list-btn">goodslist</span>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div v-if="cartList.length!=0" class="cart-item-selected">
                    <div class="cart-item-selected-left" @click="toggleAllFlag">
                        <div class="cart-item-icon icon-checkedAll" :class="{'activeCheckedAll': checkedAllFlag}">
                            <svg class="icon" v-if="checkedAllFlag" :class="{'activeCheckedIcon':checkedAllFlag}"
                                 aria-hidden="true">
                                <use xlink:href="#iconzhengque"></use>
                            </svg>
                        </div>
                        <span class="item-selected-ok-text">Select All</span>
                    </div>
                    <div class="cart-item-total">
                        <span class="cart-item-total-text">Item Total: </span>
                        <span class="cart-item-total-price-total">{{totalPrice | currency('¥')}}</span>
                        <span class="btn btn--red cart-item-checkout-btn" @click="checkout"
                              v-if="totalPrice">CHECKOUT</span>
                        <span class="btn cart-item-checkout-btn-no" v-if="!totalPrice">CHECKOUT</span>
                    </div>
                </div>
                <div v-if="cartList.length!=0" class="cart-item-selected-response">
                    <div class="cart-item-selected-left" @click="toggleAllFlag">
                        <div class="cart-item-icon icon-checkedAll" :class="{'activeCheckedAll': checkedAllFlag}">
                            <svg class="icon" v-if="checkedAllFlag" :class="{'activeCheckedIcon':checkedAllFlag}"
                                 aria-hidden="true">
                                <use xlink:href="#iconzhengque"></use>
                            </svg>
                        </div>
                        <span class="item-selected-ok-text">Select All</span>
                        <div class="right-text-price">
                            <span class="cart-item-total-text">Item Total: </span>
                            <span class="cart-item-total-price-total">{{totalPrice | currency('¥')}}</span>
                        </div>
                    </div>
                    <div class="cart-item-total">
                        <!--<span :class="{'cart-item-checkout-btn':totalPrice}">CHECKOUT</span>-->
                        <span class="cart-item-checkout-btn" @click="checkout" v-if="totalPrice">CHECKOUT</span>
                        <span class="cart-item-checkout-btn-no" v-if="!totalPrice">CHECKOUT</span>
                    </div>
                </div>
            </div>
        </div>
        <mooc-mall-footer></mooc-mall-footer>
    </div>
</template>

<script>
    import axios from 'axios'
    import MoocMallHeader from '@/components/MoocMallHeader'
    import MoocMallBread from '@/components/MoocMallBread'
    import MoocMallFooter from '@/components/MoocMallFooter'

    export default {
        name: 'Cart',
        data () {
            return {
                // routeText: 'Cart',
                selectedAll: false,
                cartList: []
            }
        },
        computed: {
            checkedAllFlag () {
                return this.checkedCount == this.cartList.length
            },
            checkedCount () {
                let i = 0
                this.cartList.forEach((item) => {
                    if (item.checked == '1') {
                        i++
                    }
                })
                return i
            },
            totalPrice () {
                let AllMoney = 0
                this.cartList.forEach((item) => {
                    if (item.checked == '1') {
                        AllMoney += parseFloat(item.salePrice) * parseInt(item.productNum)
                    }
                })
                return AllMoney
            }
        },
        components: {
            MoocMallHeader,
            MoocMallBread,
            MoocMallFooter
        },
        mounted () {
            this.initCartList()
        },
        methods: {
            initCartList () {
                axios.get('/users/cartList').then(response => {
                    let res = response.data
                    if (res.status == '0') {
                        this.cartList = res.result
                    }
                })
            },
            // 全选，反选
            toggleAllFlag () {
                // 将全选取反
                let Flag = !this.checkedAllFlag
                this.cartList.forEach((item) => {
                    item.checked = Flag ? '1' : '0'
                })
                axios.post('/users/changeCheckAll', {
                    checkedAll: Flag
                }).then(response => {
                    let res = response.data
                    // console.log(res)
                })
            },
            // 选中商品/增加商品数量
            editCart (flag, item) {
                if (flag == 'checked') {
                    item.checked = item.checked == '0' ? '1' : '0'
                } else {
                    item.productNum = flag
                }
                axios.post('/users/editCart', {
                    productId: item._id,
                    checked: item.checked,
                    productNum: item.productNum
                }).then((response) => {
                    let res = response.data
                })
            },
            // 删除条目(商品)
            delCartItemConfrim (productId) {
                let v = this
                this.$Modal.confirm({
                    title: '提示',
                    content: '确定要删除此商品吗?',
                    closable: true,
                    onOk: function () {
                        axios.post('/users/delCartItem', {
                            productId: productId
                        }).then((response) => {
                            let res = response.data
                            if (res.status == '0') {
                                v.initCartList()
                                v.$message({
                                    type: 'success',
                                    message: '删除成功!'
                                })
                            }
                        })
                    }
                })
                /*this.$confirm(`删除该商品, 确定吗?`, '警告', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    customClass: 'eleDelItemWrapper'
                }).then(() => {
                    axios.post('/users/delCartItem', {
                        productId: productId
                    }).then((response) => {
                        let res = response.data
                        if (res.status == '0') {
                            this.initCartList()
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            })
                        }
                    })

                }).catch(() => {

                })*/
            },
            // 去结账
            checkout () {
                this.$router.push('/Address')
            }
        }
    }
</script>

<style lang="scss" scoped>
    .cart-item-icon {
        display: inline-block;
        width: 25px;
        height: 25px;
        border: 1px solid #999;
        border-radius: 50%;
        cursor: pointer;
    }

    .activeChecked {
        border: 1px solid #EE7A23;
    }

    .activeCheckedAll {
        border: 1px solid #EE7A23;
        vertical-align: middle;
        margin-top: -5px
    }

    .activeCheckedIcon {
        width: 100%;
        height: 100%;
        fill: #ffffff;
        background-color: #EE7A23;
        border-radius: 50%;
        display: block;
        /*border: 1px solid #EE7A23;*/
    }

    .cart {
        .my-cart-main {
            margin-top: 25px;

            .cart-top-text {
                h1 {
                    font-weight: 700;
                    letter-spacing: 4px;
                    color: #333333;
                }
            }

            .cart-item-wrapper {
                height: 40px;
                background-color: #605F5F;
                line-height: 40px;
                padding: 0 15px 0 170px;
                margin-top: 15px;

                ul {
                    display: flex;
                    justify-content: space-between;

                    li {
                        color: #ffffff;
                        font-size: 16px;
                        letter-spacing: 2px;
                    }

                    li:first-child {
                        width: 130px;
                    }
                }
            }

            .cart-item-selected {
                height: 60px;
                background-color: #fff;
                margin-top: 25px;
                line-height: 60px;
                padding-left: 22px;
                display: flex;
                flex-wrap: nowrap;
                justify-content: space-between;
                border: 1px solid #e9e9e9;

                .cart-item-selected-left {
                    .icon-checkedAll {
                        vertical-align: middle;
                        margin-top: -5px;
                    }

                    .item-selected-ok-text {
                        font-size: 16px;
                        /*font-weight: 600;*/
                        vertical-align: middle;
                        margin-left: 15px;
                    }
                }

                .cart-item-selected-left:hover {
                    cursor: pointer;
                    color: #d1434a;
                }

                .cart-item-total {
                    .cart-item-total-text {
                        font-size: 17px;
                        color: #999999;
                        margin-right: 10px;
                    }

                    .cart-item-total-price-total {
                        color: #D1434A;
                        font-size: 20px;
                        font-weight: 700;
                        vertical-align: top;
                        margin-right: 15px;
                    }

                    .cart-item-checkout-btn {
                        height: 100%;
                        /*min-width: auto;*/
                        padding: 0 10px;
                    }

                    .cart-item-checkout-btn-no {
                        height: 100%;
                        /*min-width: auto;*/
                        padding: 0 10px;
                        color: #fff;
                        background-color: #999;
                        border-color: #999;
                        cursor: auto;
                    }
                }
            }

            .cart-item-list-wrapper {
                border: 1px solid #e9e9e9;
                background-color: #fff;

                .cart-item-list {
                    ul {
                        .cart-item-li-product {
                            display: flex;
                            padding: 22px;
                            justify-content: space-between;
                            /*justify-items: center;*/
                            align-items: center;
                            border-bottom: 1px solid #e9e9e9;

                            .cart-tab-desc {
                                min-height: 72px;

                                .cart-tab-desc-check {
                                    display: inline-block;
                                    vertical-align: middle;
                                }

                                .cart-tab-desc-pic {
                                    display: inline-block;
                                    vertical-align: middle;
                                    width: 80px;
                                    height: 80px;
                                    overflow: hidden;
                                    border: 1px solid #e9e9e9;
                                    margin-left: 20px;

                                    img {
                                        width: 100%;
                                        height: 100%;
                                    }
                                }

                                .cart-tab-desc-title {
                                    display: inline-block;
                                    vertical-align: middle;
                                    font-weight: 700;
                                    font-size: 17px;
                                    color: #000;
                                    margin-left: 20px;
                                }
                            }

                            .cart-tab-price {
                                font-size: 17px;
                                font-weight: 700;
                                color: #000;
                            }

                            .cart-tab-subtotal {
                                color: #D1434A;
                                font-size: 17px;
                                font-weight: 700;
                            }

                            .cart-tab-edit {
                                .icon-delete {
                                    font-size: 30px;
                                    /*fill: #D1434A;*/
                                    cursor: pointer;

                                    &.icon-delete:hover {
                                        fill: #D1434A;
                                        transition: all .3s ease-out;
                                    }
                                }

                            }
                        }

                        .cart-item-li-product:last-child {
                            border-bottom: none;
                        }

                        .noProduct {
                            text-align: center;
                            padding: 55px;
                            font-size: 18px;

                            .no-product-span {
                                margin-top: 15px;
                            }

                            .go-goods-list-btn-wrapper {
                                margin: 25px auto 0;

                                .go-goods-list-btn {
                                    background-color: #D1434A;
                                    border: 1px solid #D1434A;
                                    color: #fff;

                                    &:hover {
                                        background-color: #F16F75;
                                        border-color: #F16F75;
                                    }

                                    /*float: right;*/
                                }

                                span {
                                    display: inline-block;
                                    font-family: moderat, sans-serif;
                                    min-width: 195px;
                                    padding: 6px 12px;
                                    text-align: center;
                                    font-size: 15px;
                                    font-weight: 700;
                                    letter-spacing: 4px;
                                    cursor: pointer;
                                    text-transform: uppercase;
                                    -webkit-transition: all .4s ease-in-out;
                                    -moz-transition: all .4s ease-in-out;
                                    -ms-transition: all .4s ease-in-out;
                                    -o-transition: all .4s ease-in-out;
                                    transition: all .4s ease-in-out;
                                }
                            }
                        }
                    }
                }
            }

            .cart-item-list-wrapper-response {
                display: none;
            }

            .cart-item-selected-response {
                display: none;
            }
        }
    }
</style>
